import React from 'react';
import {Helmet} from "react-helmet";
import Logo from '../../../assets/Logo.png'

const MetaTag = ({title}) => {
    const seo = {
        title: title,
        description:
            "Điện máy Việt Nhật chuyên kinh doanh máy thực phẩm, thiết bị nhà bếp, máy nấy phở, máy vặt lông gà, vịt,... với chất lượng tốt nhất và phong cách phục vụ chuyên nghiệp!",
        url: "https://dienmayvietnhat.com/",
        image: "https://shopadmin.s3-ap-southeast-1.amazonaws.com/logo/Logo-share.png"
    };
    return (
        <Helmet
            title={seo.title}
            meta={[
                {
                    name: "description",
                    property: "og:description",
                    content: seo.description
                },
                {property: "og:title", content: seo.title},
                {property: "og:url", content: seo.url},
                {property: "og:image", content: seo.image},
                {property: "og:image:type", content: "image/png"},
                {property: "twitter:image:src", content: seo.image},
                {property: "twitter:title", content: seo.title},
                {property: "twitter:description", content: seo.description},
                {property: "twitter:card", content: "summary_large_image"}
            ]}/>
    )
        ;
};

export default MetaTag;